import React, { useEffect, useState } from "react";
import "./Jobs.css";
import Grid from "@mui/material/Grid";
import Header from "../../Components/header/Header";
import Footer from "../../Components/Footer/Footer";
import Button from "@mui/material/Button";
import axios from "axios";

import { Link, useParams } from "react-router-dom";
import Slide from "@mui/material/Slide";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import Pagination from "./Pagination";
import Filter from "./Filter";
import Backdrop from "@mui/material/Backdrop";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Fade from "@mui/material/Fade";
import Typography from "@mui/material/Typography";
import { useContext } from "react";
import { SearchContext } from "../../Context/SearchContext";
import JobIcon from "./JobIcon";

function Jobs() {
  // const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage, setPostsPerPage] = useState(10);

  const base_job = "http://localhost:5000/api/jobs/apply";

  const {
    handleSearch,
    posts,
    setPosts,
    closeModal,
    openModal,
    modal,
    setModal,
    noInput,
    modalData,
    data,
    clearQuery,
  } = useContext(SearchContext);

  const [results, setResults] = useState(false);

  // const [data, setData] = useState([]);

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
  };

  useEffect(() => {
    const handleUID = async () => {
      const usr = sessionStorage.getItem("uid");
      setUid(usr);
    };
    handleUID();
  });

  // const [modal, setModal] = React.useState(false);
  // const [modalData, setModalData] = React.useState("");

  const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("md"));
  const [selectedJob, setSelectedJob] = useState(null);
  const [button, setButton] = useState("Apply Now");

  const [Uid, setUid] = useState(sessionStorage.getItem("uid"));

  const handleUid = () => {
    const User = sessionStorage.getItem("uid");
    if (User) {
      setUid(User);
    }
  };

  console.log("uid", Uid);

  const [query, setQuery] = useState("");

  const handleClickOpen = async (job) => {
    setOpen(true);
    setSelectedJob(job);
    console.log("selected", job);
    var jobId = job._id;
    const userId = sessionStorage.getItem("uid");
    axios
      .post(base_job, {
        userId: userId,
        jobId: jobId,
      })
      .then((res) => {
        console.log("Application : Successful");
        console.log("AS", res);
      })
      .catch((err) => {
        console.log("Application : Server Error", err);
      });
  };


  const handleClose = () => {
    setOpen(false);
  };

  // const closeModal = () => {
  //   setModal(false);
  // };

  // const openModal = () => {
  //   setModal(true);
  // };

  // const handleQuery = (data) => {
  //   if (data == 101) {
  //     setModalData("Please fill atleast one query.");
  //     setModal(true);
  //   } else if (data.length > 0) {
  //     setResults(true);
  //     handleSearch();
  //     setPosts(data);
  //   } else {
  //     setModalData("No matches found.");
  //     setModal(true);
  //   }
  // };
  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });

  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = posts.slice(indexOfFirstPost, indexOfLastPost);
  // const post = { ...currentPosts };
  // const post = {...currentPosts};
  // console.log("posts",post[0].user[0]);
  // console.log("posts",post[0].user[0]);
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <div>
      <Header />
      <div className="jobs">
        <div className="jobContainer">
          <Grid container spacing={5} className="job_details">
            <Grid item xs={12} sm={12} md={12} lg={12}>
              <Filter />
            </Grid>
            {data &&
              currentPosts.map((item, id) => {
                return (
                  <Grid item xs={12} sm={6} md={6} lg={4.5} key={id}>
                    <div className="job_container">
                      <div>
                        <h2>{item.job}</h2>
                        <br />
                        <p>{item.location}</p>
                      </div>

                      <div className="qualist">
                        <h3>Qualifications </h3>
                        <p>{item.specialization}</p>
                      </div>

                      <div className="descriptions">
                        <h3>Job Description</h3>
                        <p>{item.details}</p>
                      </div>
                      <br />
                      <div className="jobs_btn">
                        <Link
                          to={`/view/job/${item.id}`}
                          target={"_blank"}
                          className="dataItem"
                        >
                          <Button variant="contained" className="view btn" >
                            view more
                          </Button>
                         
                        </Link>
                        <Dialog
                          fullScreen={fullScreen}
                          open={open}
                          onClose={handleClose}
                          aria-labelledby="responsive-dialog-title"
                          className="dialog"
                        >
                          <DialogTitle id="responsive-dialog-title">
                            {"Confirm Application"}
                          </DialogTitle>
                          <DialogContent className="confirmBox">
                            <DialogContentText>
                              <li> {selectedJob?.job}</li>
                              <li> {selectedJob?.location}</li>
                              <li>{selectedJob?.specialization}</li>
                            </DialogContentText>
                          </DialogContent>
                         
                          <DialogActions>
                       
                            <Button
                              onClick={handleClose}
                              variant="contained"
                              className="btn"
                            >
                              Apply
                            </Button>
                          </DialogActions>
                        </Dialog>
                        <JobIcon usrID={Uid} jobID={item._id} />
                      </div>
                    </div>
                  </Grid>
                );
              })}
            {/* {clearQuery} */}
          </Grid>
          <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            open={modal}
            onClose={closeModal}
            closeAfterTransition
            slots={{ backdrop: Backdrop }}
            slotProps={{
              backdrop: {
                timeout: 500,
              },
            }}
          >
            <Fade in={modal}>
              <Box sx={style}>
                <Typography
                  id="transition-modal-title"
                  variant="h6"
                  component="h2"
                  style={{ color: "rgb(214, 45, 90)", width: "100px" }}
                >
                  Result:
                </Typography>
                <Typography
                  id="transition-modal-description"
                  sx={{ mt: 2 }}
                  style={{ width: "400px" }}
                >
                  {modalData}
                </Typography>
                <Button
                  onClick={closeModal}
                  style={{
                    color: "white",
                    backgroundColor: "rgb(214, 45, 90)",
                    transform: "translateX(330px)",
                  }}
                >
                  Ok
                </Button>
              </Box>
            </Fade>
          </Modal>
          <Pagination
            postsPerPage={postsPerPage}
            totalPosts={posts.length}
            paginate={paginate}
          />
        </div>
      </div>

      <Footer />
    </div>
  );
}

export default Jobs;
