import "./Profile.css";
import img1 from "../../Assets/hospital.png";
import axios from "axios";
import { useEffect, useState } from "react";
import Grid from "@mui/material/Grid";
import Footer from "../../Components/Footer/Footer";
import Header from "../../Components/header/Header";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { JobsData } from "../Jobs/JobsData";

function HospitalProfile() {
  const [details, setDetails] = useState([]);
  const [userData, setuserData] = useState([]);

  useEffect(() => {
    const getData = async () => {
      const url = "http://localhost:5000/user/form/";
      try {
        const resp = await axios.get(url);
        setDetails(resp);
      } catch (err) {
        console.error(err);
      }
    };
    getData();
  }, []);
  useEffect(() => {
    const getData = async () => {
      const url = "http://localhost:5000/user/postRequirement";
      try {
        const resp = await axios.get(url);
        setuserData(resp);
      } catch (err) {
        console.error(err);
      }
    };
    getData();
  }, []);

  console.log("userData", userData);
  return (
    <>
      <Header />
      <div className="profile">
        <div className="profile_main">
          <div>
            <div>
              <img src={img1} alt="" className="hospitalImg" />
            </div>
            <Grid container spacing={5} className="job_details">
              <Grid item xs={12} sm={6} md={5} lg={5}>
                <div className="contents">
                  <ul>
                    <li>
                      <h3>{userData?.data?.[43]?.hospitalname}</h3>
                      <h4>{userData?.data?.[43]?.title}</h4>
                    </li>
                    <div className="Profile_list">
                      <li>
                        <h5>
                          Location:
                          <span>{userData?.data?.[43]?.location}</span>
                        </h5>
                      </li>
                      <li>
                        <h5>
                          About:
                          <span>{userData?.data?.[43]?.about}</span>
                        </h5>
                      </li>
                      <ul>
                        <li>
                          <h5>
                            EMail:<span> {details?.data?.[0]?.email}</span>
                          </h5>
                        </li>
                        <li>
                          <h5>
                            Contact:{" "}
                            <span>{userData?.data?.[43]?.contact}</span>
                          </h5>
                        </li>
                      </ul>
                    </div>
                  </ul>
                </div>
              </Grid>

              <Grid item xs={11} sm={6} md={6} lg={5}>
                <br />
                <div className="jobList">
                  &nbsp;&nbsp;&nbsp;Job Applications
                  <br /> <br />
                  {JobsData.map((item, index) => {
                    return (
                      <Accordion key={index}>
                        <AccordionSummary
                          expandIcon={<ExpandMoreIcon />}
                          aria-controls="panel1a-content"
                          id="panel1a-header"
                        >
                          <Typography>job {index + 1}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                          <Typography>
                            {JobsData.map((item, index) => {
                              return (
                                <Accordion key={index}>
                                  <AccordionSummary
                                    expandIcon={<ExpandMoreIcon />}
                                    aria-controls="panel1a-content"
                                    id="panel1a-header"
                                  >
                                    <Typography>
                                      Candidate {index + 1}
                                    </Typography>
                                  </AccordionSummary>
                                  <AccordionDetails>
                                    <Typography>Name</Typography>
                                    <Typography>cv</Typography>
                                  </AccordionDetails>
                                </Accordion>
                              );
                            })}
                          </Typography>
                        </AccordionDetails>
                      </Accordion>
                    );
                  })}
                </div>
              </Grid>
            </Grid>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default HospitalProfile;
