import React, { useState, useContext } from "react";
import "./SigninPage.css";
import LockIcon from "@mui/icons-material/Lock";
import AccountCircle from "@mui/icons-material/AccountCircle";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import InputAdornment from "@mui/material/InputAdornment";
import { Link } from "react-router-dom";
import Axios from "axios";
import { useEffect } from "react";
import Slide from "@mui/material/Slide";
import { Navigate } from "react-router-dom";
import Cookies from "js-cookie";
import { AuthContext } from "../../Context/AuthProvide";
//import swal from 'sweetalert';

import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";

const base_url = "http://localhost:5000/api/login";

function Signin() {
  const { userToken, userName, updateContext } = useContext(AuthContext);

  const [error, setError] = useState(null);
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [isSubmit, setIsSubmit] = useState(false);
  const [formErrors, setFormErrors] = useState({});
  const [response, setResponse] = useState("");
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const formValues = { password, email };
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    Axios.post(
      base_url,
      {
        email: email,
        password: password,
      },
      { withCredentials: false }
    )
      .then((res) => {
        console.log("response",res);
        const token = res.data.token;
        const usr_name = res.data.username;
        const role = res.data.role;
        const pro_id = res.data.prof_id;
        const userId = res.data.user_id;

        updateContext(token, usr_name);
        sessionStorage.setItem("token", token); 
        sessionStorage.setItem("username", usr_name);
        sessionStorage.setItem("role" , role); 
        sessionStorage.setItem("profId" , pro_id); 
        sessionStorage.setItem("uid" , userId); 

        setResponse("Success");
        setIsAuthenticated(true);
        // console.log("res", res.session.data);
        return res.data;
      })
      .catch((error) => {
        setResponse("Invalid Credentials");
        console.log("Login error:", error);
        setIsAuthenticated(false);
      });

    setFormErrors(validate(formValues));
  };

  // Get authenticated user data function
  function getAuthenticatedUser() {
    const token = sessionStorage.getItem("token");
    if (!token) {
      return Promise.reject("No token found");
    }

    return Axios
      .get("/api/user", { headers: { Authorization: token } })
      .then((response) => response.data);
  }

  useEffect(() => {
    console.log(formErrors);
    if (Object.keys(formErrors).length === 0 && isSubmit) {
      console.log(formValues);
      setIsLoggedIn(true);
    }
  }, [formErrors]);

  const validate = (values) => {
    const errors = {};

    if (!values.email) {
      errors.email = "Email is required!";
    }
    if (!values.password) {
      errors.password = "Password is required ";
    }

    return errors;
  };

  function handleLogin() {
    setIsLoggedIn(true);
  }

  const handleLogout = async () => {
    try {
      await Axios.post("http://localhost:5000/api/logout");
      // setAuthenticated(false);
    } catch (error) {
      console.error(error);
    }
  };

  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  if (isAuthenticated) {
    return <Navigate to="/" />;
  }

  return (
    <div className="loginMain">
 <div className="loginContainer">
      {Object.keys(formErrors).length === 0 && isSubmit && isLoggedIn ? (
        <div>
       
          <Dialog
            open={open}
            TransitionComponent={Transition}
            keepMounted
            onClose={handleClose}
            aria-describedby="alert-dialog-slide-description"
            id="dialogbox"
          >
            <DialogTitle style={{ color: "rgb(214, 45, 90)", width: "100px" }}>
              {"Sign in"}
            </DialogTitle>
            <DialogContent style={{ width: "400px" }}>
              <DialogContentText id="alert-dialog-slide-description">
                Successfully signed in
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Link to="/">
                <Button
                  onClick={handleClose}
                  style={{
                    color: "white",
                    backgroundColor: "rgb(214, 45, 90)",
                  }}
                >
                  Ok
                </Button>
              </Link>
            </DialogActions>
          </Dialog>
        </div>
      ) : (
        <div>
          <Dialog
            open={open}
            TransitionComponent={Transition}
            keepMounted
            onClose={handleClose}
            aria-describedby="alert-dialog-slide-description"
            id="dialogbox"
          >
            <DialogTitle style={{ color: "rgb(214, 45, 90)", width: "100px" }}>
              {"Sign in"}
            </DialogTitle>
            <DialogContent style={{ width: "400px" }}>
              <DialogContentText id="alert-dialog-slide-description">
                {response}
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Link>
                <Button
                  onClick={handleClose}
                  style={{
                    backgroundColor: "rgb(214, 45, 90)",
                  }}
                >
                  Ok
                </Button>
              </Link>
            </DialogActions>
          </Dialog>
        </div>
      )}

      <div className="login_contents">
        <form onSubmit={handleSubmit}>
          <TextField
            type="text"
            id="email"
            placeholder="E-mail"
            margin="normal"
            className="input_login"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <AccountCircle style={{ color: "rgb(214, 45, 90)" }} />
                </InputAdornment>
              ),
            }}
            variant="outlined"
            onChange={(e) => setEmail(e.target.value)}
          />
          <p className="required">{formErrors.email}</p>

          <TextField
            type="password" //UbA : changed to password from text to remove suggestions from browser.
            placeholder="Password"
            margin="normal"
            className="input_login"
            id="password"
            style={{color:'red'}}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <LockIcon style={{ color: " rgb(214, 45, 90) " }} />
                </InputAdornment>
              ),
            }}
            variant="outlined"
            onChange={(e) => setPassword(e.target.value)}
          />
          <p className="required">{formErrors.password}</p>

          <br />
          <Button
            variant="contained"
            className="login_button"
            type="submit"
            onClick={handleClickOpen}
          >
            Login
          </Button>
          <br />
          <br />
          <p>
            Don't have an account?&nbsp;&nbsp;&nbsp;
            <Link to="/signup" style={{ color: "#23BDB8", fontWeight: "600" }}>
              Sign Up
            </Link>
          </p>
        </form>
      </div>
      </div>
    </div>
  );
}

export default Signin;
