import React from "react";
import { useContext } from "react";
import { SearchContext } from "../../Context/SearchContext";
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import Grid from "@mui/material/Grid";
import "./Jobs.css";
import { Button } from "@mui/material";
import axios from "axios";
// import JobView from "./JobView";
import Jobs from "./Jobs";

function Filter(props) {
  const { handleHospitalname, handleLocation, handleSpecialization,response, handleSearch } =
    useContext(SearchContext);

  const options = ["Hospitals", "Apollo", "Aster"];
  const [value, setValue] = React.useState(null);
  const [hospitalname, setInputValue] = React.useState("");

  const options2 = ["Location", "Kochi"];
  const [value2, setValue2] = React.useState(null);
  const [location, setInputValue2] = React.useState("");
  const options3 = ["Specialization", "BAMS"];
  const [value3, setValue3] = React.useState(null);
  const [specialization, setInputValue3] = React.useState("");
  // const options4 = ["Designation", "Consultand gynecologist"];
  // const [value4, setValue4] = React.useState(options4[0]);
  // const [inputValue4, setInputValue4] = React.useState("");

  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState(null);
  // const [response, setResponse] = React.useState();
  const [openJobs, setopenJobs] = React.useState(false);

  return (
    <>
      <Grid container spacing={2} className="filterContainer">
        <Grid item xs={10} sm={4} md={3} lg={2} xl={2.5}>
          <Autocomplete
            value={value}
            onChange={(event, newValue) => {
              setValue(newValue);
            }}
            // inputValue={hospitalname}
            onInputChange={(event, newInputValue) => {
              handleHospitalname(newInputValue)
            }}
            id="controllable-states-demo"
            options={options}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Hospital Name"
                placeholder="Enter hospital name"
              />
            )}
          />
        </Grid>
        <Grid item xs={10} sm={4} md={2.5} lg={2} xl={2.5}>
          <Autocomplete
            value={value2}
            onChange={(event, newValue) => {
              setValue2(newValue);
            }}
            // inputValue={location}
            onInputChange={(event, newInputValue) => {
              handleLocation(newInputValue);
            }}
            id="controllable-states-demo"
            options={options2}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Location"
                placeholder="Enter your location"
              />
            )}
          />
        </Grid>
        <Grid item xs={10} sm={4} md={2.5} lg={2} xl={2.5}>
          <Autocomplete
            value={value3}
            onChange={(event, newValue) => {
              setValue3(newValue);
            }}
            // inputValue={specialization}
            onInputChange={(event, newInputValue) => {
              handleSpecialization(newInputValue);
            }}
            id="controllable-states-demo"
            options={options3}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Specialization"
                placeholder="Enter your specialization"
              />
            )}
          />
        </Grid>
        {/* <Grid item xs={10} sm={4} md={2.5} lg={2} xl={2}>
          <Autocomplete
            value={value4}
            onChange={(event, newValue) => {
              setValue4(newValue);
            }}
            inputValue={inputValue4}
            onInputChange={(event, newInputValue) => {
              setInputValue4(newInputValue);
            }}
            id="controllable-states-demo"
            options={options4}
            renderInput={(params) => <TextField {...params} />}
          />
        </Grid> */}
        <Grid
          item
          xs={11}
          sm={4}
          md={1}
          lg={1}
          xl={2}
          className="FilterSearchButton"
        >
          <Button
            className="btn"
            style={{ padding: "11px", width: "90%" }}
            onClick={handleSearch}
          >
            Search
          </Button>
        </Grid>
      </Grid>
      {/* {(openJobs)?(<Jobs/>) : "" } */}
    </>
  );
}

export default Filter;
