import React, { useState } from "react";
import "./Header.css";
import Button from "@mui/material/Button";
import SearchIcon from "@mui/icons-material/Search";
import ViewHeadlineIcon from "@mui/icons-material/ViewHeadline";
import CloseIcon from "@mui/icons-material/Close";
import { Link } from "react-router-dom";
import useMediaQuery from "@mui/material/useMediaQuery";
import { IconButton, Menu, MenuItem } from "@mui/material";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Typography from "@mui/material/Typography";
import logo from "../../Assets/logo.png";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import SearchFilter from "../Search/Search";
import SearchSpec from "../Search/SearchSpec";
import SearchLoc from "../Search/SearchLoc";
import Filter from "../../Pages/Jobs/Filter";
import Search from "../Banner/Search";

function Header() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  function handleLogin() {
    setIsLoggedIn(true);
  }

  function handleLogout() {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("username");
    sessionStorage.removeItem("hid");
    window.location.reload();
  }
  // function handleLogout() {
  //   setIsLoggedIn(false);
  // }

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const isSmallScreen = useMediaQuery("(max-width:750px)");

  const [menuAnchor, setMenuAnchor] = useState(null);
  const handleMenuOpen = (event) => {
    setMenuAnchor(event.currentTarget);
  };
  const handleMenuClose = () => {
    setMenuAnchor(null);
  };

  return (
    <header>
      <Link to="/">
        <img src={logo} alt="" />
      </Link>
      <Modal open={open} onClose={handleClose}>
        <Box className="searchBox">
          <Search></Search>
        </Box>
      </Modal>

      {isSmallScreen ? (
        <div className="header_open">
          <IconButton color="inherit" onClick={handleMenuOpen}>
            <ViewHeadlineIcon className="close_icon" />
          </IconButton>
          <Menu
            className="menu"
            anchorEl={menuAnchor}
            keepMounted
            open={Boolean(menuAnchor)}
            onClose={handleMenuClose}
          >
            <MenuItem className="menu_item">
              <Link to="/" onClick={handleMenuClose}>
                <CloseIcon className="close_icon" />
              </Link>
            </MenuItem>
            <MenuItem className="menu_item">
              <Link to="/">
                <p>Home</p>
              </Link>
            </MenuItem>
            <MenuItem className="menu_item">
              <Link to="/jobs">
                <p>Careers</p>
              </Link>
            </MenuItem>

            <MenuItem className="menu_item">
              <Link to="/about">
                <p>About</p>
              </Link>
            </MenuItem>
            <MenuItem className="menu_item">
              <Link to="/postjobs">
                <p>Post Requirement</p>
              </Link>
            </MenuItem>

            <MenuItem className="menu_item" onClick={handleOpen}>
              <p>Search</p>
            </MenuItem>

            <MenuItem className="menu_item">
              <Link to="/postprofile">
                <p> Post Profile</p>
              </Link>
            </MenuItem>
            <MenuItem className="menu_item">
              <Link to="/signup">
                <p>Register</p>
              </Link>
            </MenuItem>

            <MenuItem className="menu_item">
              <button className="nav-btn  nav-close-btn"></button>
            </MenuItem>
          </Menu>
        </div>
      ) : (
        <div className="header_btn">
          <Link to="/jobs">
            <Button variant="contained" className="btn">
              CAREERS
            </Button>
          </Link>
          <Button variant="contained" className="btn" onClick={handleOpen}>
            <SearchIcon />
          </Button>
          {sessionStorage.getItem("token") ? (
            <>
              <Link to="/upload/details">
                <Button variant="contained" className="btn">
                  {" "}
                  {/*  if login byrecruiter */}
                  Post Profile
                </Button>
              </Link>
            </>
          ) : (
            <>
              <Link to="/signup">
                <Button variant="contained" className="btn">
                  Post
                </Button>
              </Link>
            </>
          )}
          &nbsp;&nbsp;
          {sessionStorage.getItem("token") ? (
            <Button variant="contained" className="btn" onClick={handleLogout}>
              Logout
            </Button>
          ) : (
            ""
          )}
          {sessionStorage.getItem("token") ? (
            <Link to="/profile">
              <AccountCircleIcon className="account" />
            </Link>
          ) : (
            <div className="dropdown">
              <span>LOGIN</span>
              <div className="dropdown-content">
                <Link to="/login">
                  <span className="dropdownPost"> User Login</span>
                </Link>
                <br />

                <Link to="/hospital/login">
                  <span className="dropdownPost">Hospital Login</span>
                </Link>
              </div>
            </div>
          )}
        </div>
      )}
    </header>
  );
}

export default Header;
