import "./Profile.css";
import img1 from "../../Assets/img1.jpg";
import axios from "axios";
import { useEffect, useState } from "react";
import Grid from "@mui/material/Grid";
import Footer from "../../Components/Footer/Footer";
import Header from "../../Components/header/Header";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
// import { JobsData } from "../Jobs/JobsData";
import Cookies from "js-cookie";
import { useParams } from "react-router-dom";

function Profile(props) {
  const [details, setDetails] = useState([]);
  const [userData, setUserData] = useState([]);
  const [JobsData, setJobsData] = useState([]);
  const [user, setUser] = useState(null);
  const [proId, setProId] = useState();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const pid = sessionStorage.getItem("profId");
        const id = sessionStorage.getItem("uid");
        setProId(pid);
        // const response = await axios.post(
        //   `http://localhost:5000/api/profile/`,
        //   {
        //     proId: pid,
        //   }
        // );
        // setUserData(response.data);
        // console.log("profile", response.data);
        const res = await axios.get(
          "http://localhost:5000/api/user/jobs/" + id
        );
        setJobsData(res.data.jobApplications);

        console.log("rest", ...res.data.jobApplications);
      } catch (error) {
        console.log(error.response.data);
      }
    };

    fetchData();
  }, []);

  const handleRemove = async (jobId) => {
    try {
      const id = sessionStorage.getItem("uid");
      const response = await axios.put(
        `http://localhost:5000/api/remove/${id}/${jobId}`
      );
      console.log("Remove Response:", response.data);

      // Update the state to remove the job application from the array
      setJobsData((prevApplications) =>
        prevApplications.filter((app) => app.jobId._id !== jobId)
      );
    } catch (error) {
      console.log("Remove Application Error:", error);
    }
  };

  return (
    <>
      <Header />
      <div className="profile">
        <div className="profile_main">
          <div>
            <div className="profile_banner">
              <img src={img1} alt="" />
            </div>
            <Grid container spacing={5} className="job_details">
              <Grid item xs={12} sm={6} md={5} lg={5}>
                <div className="contents">
                  <ul>
                    <li>
                      <h3>{userData?.name}</h3>
                      <h4>{userData?.title}</h4>
                    </li>
                    <div className="Profile_list">
                      <li>
                        <h5>
                          Speciality:
                          <span>{userData?.data?.[4]?.specialization}</span>
                        </h5>
                      </li>
                      <li>
                        <h5>
                          Experience:
                          <span>{userData?.data?.[4]?.experience} years</span>
                        </h5>
                      </li>
                      <li>
                        <h5>
                          Languages: <span>English, Hindi</span>
                        </h5>
                      </li>
                      <ul>
                        <li>
                          <h5>
                            Email:<span> {details?.data?.[0]?.email}</span>
                          </h5>
                        </li>
                        <li>
                          <h5>
                            Contact: <span>{userData?.data?.[4]?.contact}</span>
                          </h5>
                        </li>
                      </ul>

                      <li>
                        <h5>
                          Address:
                          <span>{userData?.data?.[4]?.address}</span>
                        </h5>
                      </li>
                    </div>
                  </ul>
                </div>
              </Grid>

              <Grid item xs={11} sm={6} md={6} lg={5}>
                <br />
                <div className="jobList">
                  <h2>Jobs Applied</h2>
                  {JobsData.map((item, index) => {
                    return (
                      <Accordion key={index}>
                        <AccordionSummary
                          expandIcon={<ExpandMoreIcon />}
                          aria-controls="panel1a-content"
                          id="panel1a-header"
                        >
                          {/* <Typography>Job {index}</Typography> */}
                          <Typography>{item.jobId.job}</Typography>
                        </AccordionSummary>
                        <AccordionDetails>
                          <Typography>{item.jobId.hospitalname}</Typography>
                          <button
                            className="btn"
                            onClick={() => handleRemove(item.jobId._id)}
                          >
                            Remove
                          </button>
                        </AccordionDetails>
                      </Accordion>
                    );
                  })}
                </div>
              </Grid>
            </Grid>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Profile;
