import React from "react";
import "./Search.css";
import SearchFilter from "../Search/Search";
import SearchSpec from "../Search/SearchSpec";
import SearchLoc from "../Search/SearchLoc";
import axios from "axios";
import TextField from "@mui/material/TextField";
import Input from "@mui/material/Input";
import { useNavigate } from "react-router-dom";
import { useContext } from "react";
import { SearchContext } from "../../Context/SearchContext";

const ariaLabel = { "aria-label": "description" };

function Search(props) {
  const {
    handleHospitalname,
    handleLocation,
    handleSpecialization,
    response,
    handleSearch,
    clearResponse,
  } = useContext(SearchContext);

  const options = ["Hospitals", "Apollo", "Aster"];
  const [value, setValue] = React.useState(null);
  const [hospitalname, setInputValue] = React.useState("");

  const options2 = ["Location", "Kochi"];
  const [value2, setValue2] = React.useState(null);
  const [location, setInputValue2] = React.useState("");
  const options3 = ["Specialization", "BAMS"];
  const [value3, setValue3] = React.useState(null);
  const [specialization, setInputValue3] = React.useState("");

  const navigate = useNavigate();

  const inputStyle = {
    backgroundColor: "white", // specify the background color you want here
  };

  function handleKeyDown(event) {
    if (event.keyCode === 13) {
      // Enter key code
      event.preventDefault();
      handleSearch();
      navigate("/jobs");
      clearResponse();
    }
  }

  return (
    <div className="searchDiv">
      <div className="form-container">
        <div className="search_hospi">
          <h4>Hospital</h4>
          <br />
          <TextField
            id="outlined-basic"
            label="Hospitals"
            variant="outlined"
            inputProps={{ style: inputStyle }}
            // value={hospitalname}
            onChange={(event) => handleHospitalname(event.target.value)}
            onKeyDown={handleKeyDown}
          />
        </div>
        <div className="search_spec">
          <h4>Specialization</h4>
          <br />
          <TextField
            id="outlined-basic"
            label="Specialization"
            variant="outlined"
            inputProps={{ style: inputStyle }}
            // value={location}
            onChange={(event) => handleSpecialization(event.target.value)}
            onKeyDown={handleKeyDown}
          />
        </div>
        <div className="search_loc">
          <h4>Location </h4>
          <br />
          <TextField
            id="outlined-basic"
            label="Location"
            variant="outlined"
            inputProps={{ style: inputStyle }}
            onChange={(event) => handleLocation(event.target.value)}
            onKeyDown={handleKeyDown}
          />
        </div>
        <br />
        <br />
      </div>

      <div>
        <h5>
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Commodi
          excepturi atque
        </h5>
      </div>
    </div>
  );
}

export default Search;
