import React, { useEffect, useState } from "react";
import "../SignIn/SigninPage.css";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import { Link } from "react-router-dom";
import Axios from "axios";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Slide from "@mui/material/Slide";

const base_url = "http://localhost:5000/api/signup";

function HospitalRegisterForm() {
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [cpassword, setCpassword] = useState("");
  const [isSubmit, setIsSubmit] = useState(false);
  const [formErrors, setFormErrors] = useState({});

  const formValues = { password, email, name, cpassword };
  const handleSubmit = (e) => {
    e.preventDefault();

    Axios.post(base_url, {
      email: email,
      password: password,
      name: name,
      // cpassword: cpassword,
    }).then((res) => {
      console.log("success", res);
    })
    setFormErrors(validate(formValues));
    setIsSubmit(true);
  };
  useEffect(() => {
    console.log(formErrors);
    if (Object.keys(formErrors).length === 0 && isSubmit) {
      console.log(formValues);
    }
  }, [formErrors]);

  const validate = (values) => {
    const errors = {};
    const regex = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;

    if (!values.email) {
      errors.email = "Email is required!";
    } else if (!regex.test(values.email)) {
      errors.email = "This is not a valid email format!";
    }
    if (!values.password) {
      errors.password = "Password is required ";
    } else if (values.password.length < 8) {
      errors.password = "password must contain atleast 8 charater";
    } else if (!/\d/.test(values.password)) {
      errors.password = " password must contain atleast 1 number";
    } else if (!/[!@#$%&?]/g.test(values.password)) {
      errors.password = "Password must contain atleast 1 special character";
    } else if (!/[A-Z]/g.test(values.password)) {
      errors.password = "Password must contain atleast 1 capital letter";
    }

    if (!values.cpassword) {
      errors.cpassword = "Password is required";
    } 
    if (values.cpassword !== values.password) {
      errors.cpassword = "Passwords do not match";
    }

    return errors;
  };

  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div className="loginMain">
      {Object.keys(formErrors).length === 0 && isSubmit ? (
        <div>
          <Dialog
            open={open}
            TransitionComponent={Transition}
            keepMounted
            onClose={handleClose}
            aria-describedby="alert-dialog-slide-description"
            id="dialogbox"
          >
            <DialogTitle style={{ color: "rgb(214, 45, 90)", width: "100px" }}>
              {"Sign up"}
            </DialogTitle>
            <DialogContent style={{ width: "400px" }}>
              <DialogContentText id="alert-dialog-slide-description">
                successfully signed in
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              
              <Link to="/postJobs">  
                <Button
                  onClick={handleClose}
                  style={{
                    backgroundColor: "rgb(214, 45, 90)",
                  }}
                >
                  <Link
                    to="/postJobs"
                    style={{
                      color: "#F5FFFA",
                    }}
                  >
                    Ok
                  </Link>
                </Button>
              </Link>
            </DialogActions>
          </Dialog>
        </div>
      ) : (
        <div></div>
      )}

      <div className="login_contents">
        <form onSubmit={handleSubmit}>
          {/* <TextField
            type="text"
            id="name"
            placeholder="Name"
            margin="normal"
            className="input_login"
            variant="outlined"
            onChange={(e) => setName(e.target.value)}
          />
<br />     */}
      <TextField
            type="text"
            id="email"
            placeholder="E-mail"
            margin="normal"
            className="input_login"
            variant="outlined"
            onChange={(e) => setEmail(e.target.value)}
          />

          <p className="required">{formErrors.email}</p>
      
          <TextField
            type="text"
            placeholder="Password"
            margin="normal"
            className="input_login"
            variant="outlined"
            onChange={(e) => setPassword(e.target.value)}
          />
          <p className="required">{formErrors.password}</p>
          <TextField
            type="text"
            placeholder="Confirm Password"
            margin="normal"
            className="input_login"
            variant="outlined"
            onChange={(e) => setCpassword(e.target.value)}
          />

          <p className="required">{formErrors.cpassword}</p>
          <br />

          <br />
          <Button
            variant="contained"
            className="login_button"
            type="submit"
            onClick={handleClickOpen}
          >
            Sign Up
          </Button>
          <br />
          <br />
          <p>
            Already have an account?&nbsp;&nbsp;&nbsp;
            <Link to="/login" style={{ color: "#23BDB8", fontWeight: "600" }}>
              Sign in
            </Link>
          </p>
        </form>
      </div>
    </div>
  );
}

export default HospitalRegisterForm;
