import React, { useState } from "react";
import TextField from "@mui/material/TextField";
import "./PostJob.css";
import Grid from "@mui/material/Grid";
import Footer from "../../Components/Footer/Footer";
import Header from "../../Components/header/Header";
import Button from "@mui/material/Button";
import Axios from "axios";
import { Link } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Slide from "@mui/material/Slide";

function PostDetails() {
  const url = "http://localhost:5000/api/post/job";


  const [location, setLocation] = useState("");
  const [image] = useState("");
  const [about, setAbout] = useState("");
  const [hospitalname, setHospitalname] = useState("");

  const [isSubmit, setIsSubmit] = useState(false);
  const [formErrors, setFormErrors] = useState({});
  const formValues = {
    image,
    about,
    hospitalname,
    location
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    Axios.post(url, {
      location: location,

      image: image,
      about: about,
      hospitalname: hospitalname,
    }).then((res) => {
      console.log("success", res);
    });

    setFormErrors(validate(formValues));
    setIsSubmit(true);
  };

  const validate = (values) => {
    const errors = {};

    if (!values.location) {
      errors.location = "*  ";
    }

    if (!values.hospitalname) {
      errors.hospitalname = "* ";
    }


    // if (!values.image) {
    //   errors.image = "*";
    // }
    if (!values.about) {
      errors.about = "*";
    }

    return errors;
  };
  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const [fileName, setFileName] = useState("");

  function handleFileInputChange(event) {
    setFileName(event.target.files[0].name);
  }
  return (
    <>
      {Object.keys(formErrors).length === 0 && isSubmit ? (
        <div>
          <Dialog
            open={open}
            TransitionComponent={Transition}
            keepMounted
            onClose={handleClose}
            aria-describedby="alert-dialog-slide-description"
            id="dialogbox"
          >
            <DialogTitle style={{ color: "rgb(214, 45, 90)", width: "100px" }}>
              {"Upload"}
            </DialogTitle>
            <DialogContent style={{ width: "400px" }}>
              <DialogContentText id="alert-dialog-slide-description">
                Details uploaded successfully
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Link to="/">
                <Button
                  onClick={handleClose}
                  style={{
                    backgroundColor: "rgb(214, 45, 90)",
                  }}

                >
                  <Link             
                    to="/postProfile"   //if login by user
                    style={{
                      color: "#F5FFFA",
                    }}
                  >
                    Ok
                  </Link>
                </Button>
              </Link>
            </DialogActions>
          </Dialog>
        </div>
      ) : (
        <div></div>
      )}
      <Header />
      <div className="outsideDiv">
        <div className="postJobContainer">
          <h2>Post Details</h2>
          <form action="" onSubmit={handleSubmit}>
            <Grid container spacing={0}>
            <Grid item xs={12} lg={6} xl={6}>
                <span className="error">{formErrors.job}&nbsp;</span>
                <TextField
                  id="standard-basic"
                  label="Name"
                  variant="standard"
                  onChange={(e) => setHospitalname(e.target.value)}
                />
                 <span className="error">{formErrors.hospitalname}&nbsp;</span>
              </Grid>
              <Grid item xs={0} sm={1.8} md={3} lg={0.1} xl={0.9}></Grid>
              <Grid item xs={10} sm={7} md={5} lg={4} xl={2.8}>
                <span className="error">{formErrors.location}&nbsp;</span>
                <TextField
                  id="standard-basic"
                  label="Location"
                  variant="standard"
                  onChange={(e) => setLocation(e.target.value)}
                />
              </Grid>
            
              <Grid item xs={12} lg={4.8}>
                <label className="custom-file-upload">
                  <input type="file" onChange={handleFileInputChange} />
                  Upload Image
                  {fileName && (
                    <span className="file-name">
                      : <b>{fileName}</b>
                    </span>
                  )}
                </label>
              </Grid>
              
              
              <Grid item xs={0} sm={1.8} md={3} lg={7.2} xl={7.2}></Grid>
              <Grid item xs={0} sm={1.8} md={3} lg={1} xl={0.9}></Grid>
              <Grid item sm={10} xs={12} lg={9.5}>
                <span className="error">{formErrors.about}&nbsp;</span>
                <TextField
                  className="largeText"
                  fullWidth
                  id="standard-basic"
                  label="About"
                  variant="standard"
                  multiline
                  maxRows={3}
                  onChange={(e) => setAbout(e.target.value)}
                />
              </Grid>
              <Grid item xs={12} lg={12}>
                <Button
                  variant="contained"
                  type="submit"
                  onClick={handleClickOpen}
                  className="buttonstyle"
                >
                  Upload
                </Button>
                &nbsp;&nbsp;
                <Button variant="contained" className="buttonstyle">
                  <Link to="/" style={{ color: "white" }}>
                    Cancel
                  </Link>
                </Button>
              </Grid>
       
              </Grid>
          </form>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default PostDetails;
